
import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  root: { 
    position: 'relative',
    width: '100%',
    maxWidth: 500, 
    left: 0,  
    top:50, 
  },
});

export default function Types() {
  const classes = useStyles();

  return (  
     <div className={classes.root}>
        <Typography variant="h3" gutterBottom>
          Welcome to my interactive resume.
        </Typography>
        <Typography variant="body1" gutterBottom>
          My name is Sid and I am an undergrad at UIUC majoring in Computer Engineering. In my first  
          two years of college I have taken foundational courses in both Electrical Engineering and 
          Computer Science, and I plan on taking coursework in artificial intelligence, natural langauge 
          processing, and machine learning.I am currently looking for a SWE internship for Summer 2022. 
        </Typography>
      </div>   

  );
}